import React, { Component } from 'react';
import WeatherCard from './WeatherCard';
import WeatherByHours from './WeatherByHours';
import './weatherFor1Day.css';
import { getTime } from '../../utils/secondaryFunctions';

class WeatherFor1Day extends Component {
    render() {
        if (this.props.weatherFor1Day !== undefined && this.props.weatherFor5Hours !== undefined) {
            const weatherFor1Day = this.props.weatherFor1Day;
            const weatherByHours = this.props.weatherFor5Hours;

            return (
                <div className="weather-for-1-day">
                    <div className="location">{this.props.location}</div>
                    <div className="time">{getTime()}</div>
                    <WeatherCard weather={weatherFor1Day} />
                    <WeatherByHours weather={weatherByHours} />
                </div>
            );
        } else {
            return (
                <div className="weather-card">
                    <div className="select-city">Select city</div>
                </div>
            );
        }
    }
}

export default WeatherFor1Day;


