import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {reformationWeatherData} from './utils/secondaryFunctions';
import * as locationActions from './store/actions/LocationActions';
import * as weatherActions from './store/actions/WeatherActions';
import Header from './components/header/Header';
import LocationForm from './components/location/LocationForm';
import Tabs from './components/tabs/Tabs';
import WeatherFor1Day from './components/weather/WeatherFor1Day';
import WeatherFor5Days from './components/weather/WeatherFor5Days'

class App extends Component {
    render() {
        const { setCity } = this.props.locationAction;
        const location = this.props.location;
        const { setWeatherFor1Day, setWeatherFor5Days } = this.props.weatherActions;
        let {weatherFor1Day ,weatherFor5Days } = this.props.weather;
        let weatherFor5Hours;

        if (weatherFor5Days !== undefined) {
            weatherFor5Hours = weatherFor5Days.slice(0, 6);
            weatherFor5Days = reformationWeatherData(weatherFor5Days);
        }

        return (
            <div className="App">
                <Header/>
                <LocationForm
                    setCity={setCity}
                    setWeatherFor1Day={setWeatherFor1Day}
                    setWeatherFor5Days={setWeatherFor5Days}
                    location={location}
                />
                <Tabs
                    titles={['One day', 'Five days']}
                >
                    <WeatherFor1Day
                        weatherFor5Hours={weatherFor5Hours}
                        weatherFor1Day={weatherFor1Day}
                        location={location}
                    />
                    <WeatherFor5Days
                        weatherFor5Days={weatherFor5Days}
                        location={location}
                    />
                </Tabs>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return {
        location: state.location.city,
        weather: state.weather
    }
}

function mapDispatchToProps(dispatch) {
    return {
        locationAction: bindActionCreators(locationActions, dispatch),
        weatherActions: bindActionCreators(weatherActions, dispatch)
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);



