import React, {Component} from 'react'
import './weatherFor5Days.css'

class WeatherFor5Days extends Component {
    render() {
        /*tempMax: tmpMax,
         tempMin: tmpMin,
         humidity: humidity / j,
         pressure: pressure / j,
         icon: data[0].weather[0].icon,
         date: data[i].dt_txt.split(' ')[0]*/

        if (this.props.weatherFor5Days !== undefined) {
            return (
                <div className="weather-for-5-days">
                    <div className="location">{this.props.location}</div>
                    <table className="weather-for-5-days__list">
                        <thead>
                        <tr>
                            <td/>
                            <td>min</td>
                            <td>max</td>
                            <td>humidity</td>
                            <td>mmHg</td>
                            <td/>
                        </tr>
                        </thead>
                        <tbody>
                        {this.props.weatherFor5Days.map((item, index) => {
                            return (
                                <tr key={index} className="weather-for-5-days__item">
                                    <td className="icon"><img src={item.icon} alt="ico"/></td>
                                    <td className="temp-max">{item.tempMin}</td>
                                    <td className="temp-min">{item.tempMax}</td>
                                    <td className="humidity">{item.humidity}</td>
                                    <td className="pressure">{item.pressure}</td>
                                    <td className="date">{item.date}</td>
                                </tr>
                            );
                        })}
                        </tbody>
                    </table>
                </div>
            );
        } else {
            return (
                <div className="weather-for-5-days">
                    <div className="select-city">Select city</div>
                </div>
            );
        }
    }
}

export default WeatherFor5Days;


